import React, { useState, useEffect } from 'react'
import { Container, Card, Button, Navbar } from 'react-bootstrap'
import { Provider, useDispatch, useSelector } from 'react-redux'
import { AddTask, RemoveTask, DoneTask, UnDoneTask, ReStore, MoveDown, MoveUp } from './Store/actions'
import Task from './Components/task'
import store from './Store/store'
import Counter from './Components/counter'
import './App.css'

function App() {
  const tasks = useSelector((state) => state)
  const dispatch = useDispatch()
  const [value, setValue] = useState('')

  useEffect(() => {
    const restate = JSON.parse(localStorage.getItem('store'))
    dispatch(ReStore(restate))
    // console.log(restate)
    // eslint-disable-next-line
  }, [])

  const style = {
    // width: '25rem',
    flexDirection: 'row',
    justifyContent: 'space-between',
  }

  function press(e) {
    if (e.key === 'Enter') {
      value && dispatch(AddTask(value))
      setValue('')
    }
  }

  return (
    <div>
      <Navbar variant='light' bg='light'>
        <Container>
          <Navbar.Brand>To Do List</Navbar.Brand>
        </Container>
      </Navbar>
      <Container>
        <Counter />
        <Card style={style} className=' mt-2 p-1'>
          <input
            className='outline'
            style={{
              width: '100vw',
              border: 'none',
            }}
            onChange={(e) => {
              setValue(e.target.value)
            }}
            value={value}
            onKeyDown={press}
          />
          <Button
            size='sm'
            variant='outline-success'
            onClick={(e) => {
              value && dispatch(AddTask(value))
              setValue('')
            }}
            style={{
              width: 'fit-content',
            }}
          >
            Add
          </Button>
        </Card>
        {tasks.map((task, key) => (
          <Task
            task={task}
            key={key}
            index={key}
            onDone={(e) => {
              dispatch(DoneTask(task.id))
            }}
            onUnDone={(e) => {
              dispatch(UnDoneTask(task.id))
            }}
            onClick={(e) => {
              dispatch(RemoveTask(task.id))
            }}
            onUp={(e) => {
              dispatch(MoveUp(task.id, key))
            }}
            onDown={(e) => {
              dispatch(MoveDown(task.id, key))
            }}
          />
        ))}
      </Container>
    </div>
  )
}

function AppWithStore() {
  return (
    <Provider store={store}>
      <App />
    </Provider>
  )
}

export default AppWithStore
