import * as actions from './actionTypes'

export let lastId = 0

lastId = localStorage.getItem('lastId')

// const s = []
// for (let i = 0; i < 6; i++) {
//   s[i] = { id: i, text: 'number' + i, statu: 'pinding' }
// }

export default function reducer(state = [], action) {
  switch (action.type) {
    case actions.ADD_TASK:
      return [
        ...state,
        {
          id: lastId++,
          text: action.payload.text,
          statu: 'pinding',
        },
      ]
    case actions.REMOVE_TASK:
      return state.filter((task) => task.id !== action.payload.id)
    case actions.DONE_TASK:
      return state.map((task) => (task.id !== action.payload.id ? task : { ...task, statu: 'done' }))
    case actions.UN_DONE_TASK:
      return state.map((task) => (task.id !== action.payload.id ? task : { ...task, statu: 'pinding' }))
    case actions.RESTORE_TASKS:
      return [...action.payload]
    case actions.TASK_UP:
      const task = state.filter((task) => task.id === action.payload.id)
      const tasks = state.filter((task) => task.id !== action.payload.id)
      return [...tasks.slice(0, action.payload.index - 1), ...task, ...tasks.slice(action.payload.index - 1)]
    case actions.TASK_DOWN:
      const task0 = state.filter((task) => task.id === action.payload.id)
      const tasks0 = state.filter((task) => task.id !== action.payload.id)
      return [...tasks0.slice(0, action.payload.index + 1), ...task0, ...tasks0.slice(action.payload.index + 1)]
    default:
      return state
  }
}
