import React from 'react'
import { Card, Button, ButtonGroup } from 'react-bootstrap'
import { useSelector } from 'react-redux'

export default function Task({ task, index, onClick, onDone, onUnDone, onUp, onDown }) {
  const state = useSelector((state) => state)

  const style = {
    // width: '25rem',
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'baseline',
  }
  const column = {
    flexDirection: 'column',
    display: 'flex',
    alignItems: 'center',
  }

  let done = {}
  let disable = false
  const buttonStyle = { width: 'fit-content', height: '2rem', marginBottom: '.5rem' }

  if (task.statu === 'done') {
    done = {
      textDecoration: 'line-through',
      color: 'gray',
    }
    disable = true
  }

  return (
    <div>
      <Card style={style} className=' mt-2 p-1'>
        <Card.Title className='m-2'>Task #{index + 1}</Card.Title>
        <Card.Text style={done}>
          <span className='m-2'>{task.text ? task.text : 'New Task...'}</span>
        </Card.Text>
        <span style={column}>
          <ButtonGroup>
            {task.statu === 'done' ? (
              <Button onClick={onUnDone} size='sm' variant='outline-dark' style={buttonStyle}>
                UnDone
              </Button>
            ) : (
              <Button disabled={disable} onClick={onDone} size='sm' variant='outline-primary' style={buttonStyle}>
                Done
              </Button>
            )}
            <Button onClick={onClick} size='sm' variant='outline-danger' style={buttonStyle}>
              Remove
            </Button>
          </ButtonGroup>
          <ButtonGroup>
            <Button disabled={index === 0} onClick={onUp} size='sm' variant='outline-info' style={buttonStyle}>
              ⬆
            </Button>
            <Button disabled={index === state.length - 1} onClick={onDown} size='sm' variant='outline-secondary' style={buttonStyle}>
              ⬇
            </Button>
          </ButtonGroup>
        </span>
      </Card>
    </div>
  )
}
